.generate-cover-letter-container {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    background: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    font-family: Arial, sans-serif;
}

h2, h3 {
    margin-bottom: 20px;
    color: #333333;
}

.user-info input, .section input {
    width: 100%;
    padding: 10px;
    margin: 5px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.section {
    margin-bottom: 20px;
}

.section label {
    font-size: 18px;
    font-weight: bold;
    display: block;
    margin-bottom: 10px;
}

.subsection {
    margin-left: 20px;
}

.subsection label {
    display: block;
    margin-bottom: 5px;
    font-size: 16px;
}

.cover-letter-templates {
    margin-top: 30px;
}

.template-option {
    display: block;
    margin-bottom: 10px;
}

.generate-btn {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
}

.generate-btn:hover {
    background-color: #0056b3;
}
